import { notFound } from 'next/navigation';
import PDFViewer from '@/components/pdf-viewer';
import type { Subject } from '@/hooks/use-unlock-store';

const VALID_SUBJECTS: Subject[] = ['science', 'math', 'social-studies'];

export function generateStaticParams() {
  return VALID_SUBJECTS.map((subjectName) => ({
    subjectName,
  }));
}

export default function SubjectPage({ params }: { params: { subjectName: string } }) {
  const subjectName = params.subjectName as Subject;

  if (!VALID_SUBJECTS.includes(subjectName)) {
    notFound();
  }

  return <PDFViewer subjectName={subjectName} />;
}
